<template>
  <div>
    <div class="hello_world" @click="toTop" v-if="scrollTopShow == true" title="滚动到顶部">
      <a-icon type="up-circle" :style="{ fontSize: '50px' }" />
    </div>
  </div>
</template>       

<script>
export default {
  data() {
    return { scrollTopShow: false };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      let that = this;
      window.onscroll = function () {
        let distance =
          document.documentElement.scrollTop || document.body.scrollTop;
        console.log("Turbo ~ init ~ distance", distance);
        // let height = document.body.clientHeight || window.screen.availHeight;
        // console.log("Turbo ~ init ~ height", height);
        if (distance > 200) {
          that.scrollTopShow = true;
          console.log("Turbo ~ init ~ scrollTopShow", that.scrollTopShow);
        } else {
          that.scrollTopShow = false;
        }
      };
    },
    toTop() {
      document.documentElement.scrollIntoView({ behavior: "smooth" });
    },
  },
};
</script>

<style lang="scss" scoped>
.hello_world {
  position: fixed;
  top: 400px;
  right: 20px;
  width: 50px;
  height: 50px;
  background-color: green;
  border-radius: 25px;
  z-index: 99;
  .top {
    width: 50px;
    height: 50px;
  }
}
</style>>





