<template>
  <div class="top">
    <div>网站首页</div>
    <div>后台模式</div>
  </div>
</template>

<script>
export default {
  name: "top",
  data() {
    return {};
  },
  mounted() {},
  methods: {},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" >
.top {
  position: fixed;
  top: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: HYRunYuan-EEW, HYRunYuan;
  background: black;
  color: white;
}
.topClose {
  position: absolute;
  top: -80px;
}
</style>



