<template>
  <div class="hello">
    <button @click="test2()">滑动</button>
    <div class="hello1" id="test1">test1aaa</div>
    <div class="hello2" id="test2" @click="test2()">test2aaa11</div>
    <div v-for="(item, index) in list" :key="index" @click="change(item)">
      {{ item.t1 }}
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld2",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      list: [{ t1: "t1" }, { t1: "t1" }, { t1: "t1" }],
    };
  },  
  mounted: {},
  methods: {        
    test1() {
      document.getElementById("test1").scrollIntoView();
    },
    test2() {
      document
        .getElementById("test2")
        .scrollIntoView({ behavior: "smooth", block: "start" });
        cn
    },
    change(item) {
      item.t1 = "111";
      this.$forceUpdate();
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.hello1 {
  width: 200px;
  height: 300px;
  background-color: #42b983;
}
.hello2-su {
  width: 200px;
  height: 300px;
  background-color: blue;
}
button {
  height: 100px;
}
</style>



